import { authConfig } from "./config/auth.confg";
import NextAuth from "next-auth";
import Credential from "next-auth/providers/credentials";
import { NextResponse } from "next/server";


const loggedInAsAdminPath = ["/admin"];
const loggedInAsStaffPath = ["/staff"];
const loggedOutPath = ["/login", "/register", "/forget-password"];

export const { handlers, signIn, signOut, auth } = NextAuth({
  ...authConfig,
  callbacks: {
    async authorized({ auth, request: { nextUrl } }) {
      const isLoggedIn = !!auth?.user;
      console.log(nextUrl);
      if(isLoggedIn && nextUrl.pathname === '/login') return NextResponse.redirect(new URL("/home", nextUrl.origin));
      // return NextResponse.redirect(new URL('/login',nextUrl));
    },
 
  },
  providers: [
    Credential({
      authorize: async (credentials) => {
        console.log("ðŸš€ ~ authorize: ~ credintal:", credentials);
        return { user: "ramez ahmed", email: "ramezahmed@gmail.com" };
      },
    }),
  ],
  session: {
    strategy: "jwt",
    maxAge: 10 * 24 * 60 * 60,
  },
  secret: process.env.SECRET,
});
